//custom
.iframe {
  padding: 0 2rem 1rem 2rem;
}
.grid {
  padding: 0 15px;
  .title {
    padding: 2rem 0 2rem 0;
    font-size: 1.5rem;

    .anticon {
      font-size: 1.5rem;
      margin-right: 0.5rem;
    }
    i {
      font-size: 24px;
      margin-right: 3px;
    }
  }
  .toolbar {
    padding: 0 0 0.8rem 0;
    button {
      margin-right: 1rem;
      &:last-child {
        margin-right: 0 !important;
      }
    }
    .iconfont {
      margin-right: 0.3rem;
    }

    .separator {
      margin-left: 5px;
    }
  }
}
.form-edit,
.form-view {
  padding: 0 15% 0 8% !important;
}
.form-edit,
.form-view,
.form-custom {
  max-width: 1200px;
  .title {
    padding: 2rem 0;
    font-size: 1.5rem;
    .anticon {
      font-size: 1.5rem;
      margin-right: 0.5rem;
    }
    i {
      font-size: 24px;
      margin-right: 3px;
    }
  }
  .title-btn {
    padding: 1.5rem 0;
    font-size: 1.8rem;
    text-align: right;
  }

  .subtitle-edit {
    color: #0088ff;
    margin-bottom: 12px;
    padding-left: 12px;
  }
  .subtitle {
    color: #0088ff;
    margin-bottom: 12px;
  }
  .subtitle-large {
    color: #0088ff;
    margin-bottom: 12px;
