  }
  .subItemTitle {
    color: #2db7f5;
  }
  .subcontent {
    padding: 0 0 12px 0;
  }
  .ant-divider {
    margin: 12px 0 !important;
  }
  .ant-upload-list-item-thumbnail {
    max-width: 100px;
  }

  .toolbar {
    padding: 1rem 0 3rem 0;
    text-align: center;
    border-top: 1px solid #f5f5f5;
    button {
      margin-right: 1rem;
      &:last-child {
        margin-right: 0 !important;
      }
    }
    .iconfont {
      margin-right: 0.3rem;
    }
  }
  .ant-form-item-view {
    line-height: 130%;
    min-height: 40px;
    padding-top: 11px !important;
  }
}
